
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'Mine',
directives: {
TransferDom
},
components: {
Previewer,
XTextarea,
XSwitch,
XInput,
Group,
XButton
},
data () {
return {
newMessage: true,
isShow1: false,
isShow2: false,
params: {
uuid: '',
newPassword: '',
verifyCode: '',
phoneNumber: ''
},
doublePassword: ''
}
},
created(){
this.params.phoneNumber = this.$store.state.phoneNumber
},
methods: {
getUuid () {
let s = [];
let hexDigits = "0123456789abcdef";
for (var i = 0; i < 36; i++) {
s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
}
s[14] = "4";  
s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);  
s[8] = s[13] = s[18] = s[23] = "-";

let uuid = s.join("");
return uuid;
},
getCode () {
let uuid = this.getUuid()
this.$http.fetchGet('/merchant/verify/code/send',{mobile: this.params.phoneNumber,uuid: uuid}).then((res)=>{
if(res.data.code == 200) {
this.params.uuid = uuid
this.$vux.toast.show({
text: '获取验证码成功',
position: 'middle'
})
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getBack () {
this.$router.go(-1)
},

postData () {
if(!this.params.verifyCode) {
this.$vux.toast.show({
text: '请输入验证码',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.newPassword) {
this.$vux.toast.show({
text: '请输入提现密码',
position: 'middle',
type: 'warn'
})
return
}
if(!this.doublePassword) {
this.$vux.toast.show({
text: '请输入确认密码',
position: 'middle',
type: 'warn'
})
return
}
if(this.params.newPassword != this.doublePassword) {
this.$vux.toast.show({
text: '两次密码输入不一致',
position: 'middle',
type: 'warn'
})
return
}
if(this.params.newPassword.length < 6 || this.params.newPassword.length > 20) {
this.$vux.toast.show({
text: '提现密码在6-20位之间',
position: 'middle',
type: 'warn'
})
return
}
if(!(/^[0-9a-zA-Z]*$/g.test(this.params.newPassword))) {
this.$vux.toast.show({
text: '提现密码只能输入数字或字母',
position: 'middle',
type: 'warn'
})
return
}
this.$vux.loading.show({
text: '加载中...'
})
let params = this.params
params['merchantId'] = this.$store.state.merchantId
this.$http.fetchPost('/merchant/reset/pay/password', params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
this.$router.go(-1)
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
}
}



